import {useEffect, useState} from "react";
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle, FormControl,
    TextField,
    Typography
} from "@mui/material";

export default function SetLockerDialog(props) {

    const {contract_address, locker_address, onSave} = props;

    const [open, setOpen] = useState(false);
    const [val, setVal] = useState(locker_address);

    useEffect(()=>{
        if(open)setVal(locker_address);
    }, [open, locker_address])

    return <>
        <Typography onClick={() => setOpen(true)}>...{locker_address.substr(-6)}</Typography>
        <Dialog open={open}>
            <DialogTitle>Set Faucet</DialogTitle>
            <DialogContent>
                <Typography>Update Locker for: {contract_address}</Typography>
                <FormControl fullWidth>
                    <TextField
                        label="Locker Address"
                        variant="outlined"
                        value={val}
                        defaultValue={locker_address}
                        onChange={(ev) => {
                            setVal(ev.target.value)
                        }}
                    />
                </FormControl>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button onClick={() => {
                    onSave(contract_address, val);
                    setOpen(false)
                }}>Submit</Button>
            </DialogActions>
        </Dialog>
    </>;
}