import {Avatar, Button, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useDispatch, useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import LockIcon from '@mui/icons-material/Lock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import {useEffect, useState} from "react";
import Web3 from "web3";
import {dataseed} from "../utils";
import faucet_abi from '../abis/faucet_abi.json';
import {updateFaucetRewards} from "../redux/slices/divvySlice";
import StatDetail from "./StatDetail";

export default function ProjectFaucet(props) {

    const {project} = props;
    const {bnbPrice, projectsMap, wallet} = useSelector(state => state.divvy);
    const {faucet, stats} = projectsMap[project.contract_address];

    const [contract, setContract] = useState(null);
    const dispatch = useDispatch();

    // sponsor check
    const [viewable, setViewable] = useState(false);
    const [tokensNeeded, setTokensNeeded] = useState(0);
    const sponsorToken = '0xD0685D66a23F86594C5f74B69518ad32B6C2F411';
    const sponsorMin = 10;// in USD
    useEffect(() => {
        setViewable(false);
        setTokensNeeded(sponsorMin/bnbPrice/projectsMap[sponsorToken]?.stats?.price)
        if (wallet.balances[sponsorToken] && projectsMap[sponsorToken]?.stats?.price && bnbPrice) {

            const userVal = wallet.balances[sponsorToken] * projectsMap[sponsorToken]?.stats?.price * bnbPrice;

            if (userVal >= sponsorMin) {
                setViewable(true);
            } else {
                setViewable(false);
            }
        }
    }, [wallet.balances, projectsMap, bnbPrice])

    useEffect(() => {
        if (wallet.address && window.provider) {
            const web3 = new Web3(window.provider);
            setContract(new web3.eth.Contract(faucet_abi, project.faucet_address));
        }
    }, [wallet.address, project.faucet_address]);

    useEffect(() => {
        const interval = setInterval(() => {
            const c = new dataseed.eth.Contract(faucet_abi, project.faucet_address);
            c.methods.availableReflections().call((err, resp) => {
                if (err) {
                    console.log(err);
                    return;
                }
                dispatch(updateFaucetRewards({
                    contract: project.contract_address,
                    faucet_rewards: resp / Math.pow(10, 9) || 0
                }))
            })
        }, 10000);

        return () => clearInterval(interval);
    }, [dispatch, project.contract_address, project.faucet_address]);

    const claimFaucet = () => {
        if (contract) contract.methods.GetFaucetRewards().send({from: wallet.address});
    }

    if (!faucet) {
        return null
    }

    return <Paper elevation={0}>
        {
            viewable ? <List disablePadding dense subheader={<Typography>Lava Flow <Typography variant="caption" style={{float: 'right'}}>sponsored by
                    LAVA</Typography></Typography>}>
                    <StatDetail
                        avatar={<LockIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(faucet.locked.toFixed(0))} ${project.symbol}`}
                        secondary="Locked"
                    />
                    <StatDetail
                        avatar={<LocalAtmIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(faucet.reflections)} ${project.symbol}`}
                        secondary="Available"
                    />
                    <StatDetail
                        avatar={<AttachMoneyIcon sx={{color: project.color}}/>}
                        primary={`${DivvyUSDFormat(faucet.reflections * stats.price * bnbPrice)}`}
                        secondary="Value"
                    />
                    <ListItem>
                        {
                            wallet.address ? <Button
                                    variant="contained"
                                    onClick={claimFaucet}
                                    disabled={faucet.reflections <= 0}
                                >Claim {project.symbol}</Button>
                                :
                                <></>
                        }
                    </ListItem>
                </List>
                :
                wallet.address ?
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography textAlign="center">To access Lava Flow, you need to
                                hold <br/><strong>{DivvyUSDFormat(sponsorMin)}</strong> worth of LAVA.<br/><Typography textAlign="center" variant="caption">{DivvyCryptoFormat(tokensNeeded)} LAVA</Typography></Typography>


                        </Grid>
                    </Grid>
                    :
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0xD0685D66a23F86594C5f74B69518ad32B6C2F411/logo.png" />
                        </ListItemAvatar>
                        <ListItemText primary={<Typography>Connect wallet for <strong>Lava Flow</strong></Typography>} secondary="Sponsored by LAVA" />
                    </ListItem>
        }

    </Paper>
}