import WelcomeLogo from '../images/divvy_character_welcome.png';
import {
    Alert,
    Avatar,
    Button, CircularProgress,
    Container, Dialog, DialogContent,
    FormControl,
    Grid,
    Link, ListItem, ListItemAvatar, ListItemText,
    Paper,
    Snackbar,
    TextField,
    Typography
} from "@mui/material";
import {useEffect, useState} from "react";
import axios from "axios";
import Web3 from "web3";
import divvy_listings_abi from "../abis/DivvyListings.json";
import {useSelector} from "react-redux";
import {dataseed} from "../utils";

const DivvyListingContract = '0x5cf76803e834B0D16172ECeD1175F55c4881eecA';

export default function NewListing() {

    const {projectsMap, wallet} = useSelector(state => state.divvy);

    const [contractAddress, setContractAddress] = useState("");
    const [name, setName] = useState("");
    const [symbol, setSymbol] = useState("");
    const [color, setColor] = useState("#000000");

    const [FEGexAssetRepo, setFEGexAssetRepo] = useState(false);
    const [existingListing, setExistingListing] = useState(false);
    const [loading, setLoading] = useState(false);

    const [snackbar, setSnackbar] = useState({open: false, message: '', severity: undefined});
    const [dialogOpen, setDialogOpen] = useState(false);

    const web3 = new Web3(window.provider);
    const contract = new web3.eth.Contract(divvy_listings_abi, DivvyListingContract);

    const reset = () => {
        setContractAddress("");
        setName("");
        setSymbol("");
        setColor("#000000");
        setFEGexAssetRepo(false);
        setExistingListing(false);
    }

    const addListing = () => {

        let value = 100000000000000000;
        if(wallet.address === '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe') {
            value = 0;
        }

        contract.methods.addListing(contractAddress, name, symbol, color).send({from: wallet.address, value}, (err, resp) => {
            if (err) {
                setSnackbar({open: true, message: err.message, severity: 'error'});
                return;
            }
            setDialogOpen(true);
        })

    }

    useEffect(() => {
        setExistingListing(false);
        setFEGexAssetRepo(false);
        setLoading(false);

        if (contractAddress.length === 42) {

            if (projectsMap[contractAddress]) {
                setExistingListing(true);
                return;
            }

            setLoading(true);
            axios
                .get(`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${contractAddress}/info.json`)
                .then(resp => {
                    setName(resp.data.name);
                    setSymbol(resp.data.symbol);
                    setFEGexAssetRepo(true);
                    setLoading(false);
                })
                .catch(() => {
                    setFEGexAssetRepo(false);
                    setLoading(false);
                })
        }
    }, [contractAddress, projectsMap])

    return <Container maxWidth="sm">
        <Grid container spacing={2}>
            <Grid item xs={12} textAlign="center">
                <img src={WelcomeLogo} style={{margin: '0 auto', width: 200}} alt="Welcome to Divvy"/>
                <Typography variant="h6">Create New Listing</Typography>
            </Grid>
            <Grid item xs={12} textAlign="center">
                <FormControl fullWidth>
                    <TextField
                        label="Contract Address"
                        variant="outlined"
                        value={contractAddress}
                        onChange={(ev) => {
                            setContractAddress(ev.target.value)
                        }}
                    />
                </FormControl>
            </Grid>
            {
                contractAddress.length === 42 && FEGexAssetRepo && !existingListing && <>
                    <Grid item xs={12} textAlign="center">
                        <FormControl fullWidth>
                            <TextField
                                type="color"
                                label="Custom Color"
                                variant="outlined"
                                value={color}
                                onChange={(ev) => {
                                    setColor(ev.target.value)
                                }}
                            />
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} textAlign="center">
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <img
                                        src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${contractAddress}/logo.png`}
                                        alt={symbol}
                                        width={40}
                                    />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText primary={name} secondary={symbol} />
                        </ListItem>
                    </Grid>
                    <Grid item xs={12} textAlign="center">
                        {wallet.address ? <Button variant="contained" fullWidth onClick={addListing}>Add Listing</Button> :
                            <Typography>Connect Wallet to Add Listing</Typography>}
                    </Grid>
                </>
            }
            {
                contractAddress.length === 42 && !FEGexAssetRepo && !existingListing && !loading &&
                <Grid item xs={12} textAlign="center">
                    <Paper
                        style={{border: '1px solid #872eff', padding: 20, backgroundColor: 'rgba(135, 46, 255, .2)'}}>
                        <Typography>We can't find information about your project. Please make sure you add it to the
                            FEGex Asset Repo.<br/><br/> <Link underline="none"
                                                              href="https://docs.fegtoken.com/listing-on-fegex/how-to-submit-logo-and-info"
                                                              target="_blank">How-To Guide</Link></Typography>

                    </Paper>

                </Grid>
            }
            {
                contractAddress.length === 42 && existingListing && <Grid item xs={12} textAlign="center">
                    <Typography>A listing already exists for this contract.</Typography>
                    <Avatar
                        sx={{width: 100, height: 100, margin: '10px auto'}}
                    >
                        <img
                            src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${contractAddress}/logo.png`}
                            alt={projectsMap[contractAddress]?.symbol}
                            width={100}
                        />
                    </Avatar>
                    <Link underline='none'
                          href={`https://divvysd.com/dashboard/#/${projectsMap[contractAddress].symbol}`}>{projectsMap[contractAddress].symbol}</Link>
                </Grid>
            }
            <Snackbar
                open={snackbar.open}
                autoHideDuration={10000}
                onClose={() => {
                    setSnackbar({...snackbar, open: false});
                }}
            >
                <Alert variant="filled" elevation={3} severity={snackbar.severity}
                       sx={{width: '100%'}}>{snackbar.message}</Alert>
            </Snackbar>

            <SuccessDialog open={dialogOpen} close={()=>{reset();setDialogOpen(false)}} ca={contractAddress} symbol={symbol}/>
        </Grid>
    </Container>
}

function SuccessDialog(props) {

    const {open, close, ca, symbol} = props;

    const [created, setCreated] = useState(false);

    useEffect(() => {

        if (!open) return;

        const contract = new dataseed.eth.Contract(divvy_listings_abi, DivvyListingContract);

        const checkForListing = () => {
            contract.methods.getListing(ca).call((err, resp) => {
                if (resp.created) {
                    setCreated(true)
                }
            })
        }
        const interval = setInterval(checkForListing, 5000);
        return () => clearInterval(interval);

    }, [open, ca]);

    return <Dialog open={open} fullScreen PaperProps={{style: {backgroundColor: '#872eff', color: "#fff"}}}>
        <DialogContent>
            {
                created ?
                    <div style={{margin: '0 auto', width: 200, marginTop: 200, textAlign: 'center'}}>
                        <Link href={`https://divvysd.com/dashboard/#/${symbol}`} underline="none">
                            <Button variant="contained" fullWidth style={{backgroundColor: '#fff', color: "#872eff"}}>View {symbol}</Button>
                        </Link>
                        <Typography style={{color:'#FFF', marginTop: 10}} onClick={()=>{close();setCreated(false)}}>Add another listing</Typography>
                    </div>
                    :
                    <>
                        <div style={{margin: '0 auto', width: 100, marginTop: 200}}>
                            <img
                                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/0xe9490A983292bD844074Ac3e83c3cb408f578b61/logo.png`}
                                alt="Divvy Logo"
                                width={100}
                                style={{position: 'fixed', marginLeft: 10, marginTop: 10}}
                            />
                            <CircularProgress color="inherit" size={120} thickness={1}/>
                        </div>
                        <Typography textAlign="center" style={{marginLeft: 10, marginTop: 20}}>Creating
                            Listing...</Typography>
                    </>
            }

        </DialogContent>
    </Dialog>
}