import {Avatar, Grid, Link, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import StatDetail from "./StatDetail";

import {DivvyCryptoFormat, DivvyUSDFormat} from "../utils";

import sd_abi from '../abis/sd_abi.json';

import PieChartIcon from '@mui/icons-material/PieChart';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import RuleIcon from '@mui/icons-material/Rule';
import InvertColorsIcon from "@mui/icons-material/InvertColors";

import {dataseed} from "../utils";

export default function ProjectRisk(props) {

    const {project} = props;
    const {bnbPrice, projectsMap, wallet} = useSelector(state => state.divvy);
    const {stats, owner} = projectsMap[project.contract_address];

    const [ownerBalance, setOwnerBalance] = useState(0);
    const [ownerPercentage, setOwnerPercentage] = useState(0);

    const [poolDiff, setPoolDiff] = useState(0);
    const [poolDiffPercentage, setPoolDiffPercentage] = useState(0);

    const [liquidityScore, setLiquidityScore] = useState("");

    // sponsor check
    const [viewable, setViewable] = useState(false);
    const [tokensNeeded, setTokensNeeded] = useState(0);
    const sponsorToken = '0xCfE058c0646c0Eec7e042744D9B8787d0f047a34';
    const sponsorMin = 10;// in USD
    useEffect(() => {
        setViewable(false);
        setTokensNeeded(sponsorMin / bnbPrice / projectsMap[sponsorToken]?.stats?.price)
        if (wallet.balances[sponsorToken] && projectsMap[sponsorToken]?.stats?.price && bnbPrice) {

            const userVal = wallet.balances[sponsorToken] * projectsMap[sponsorToken]?.stats?.price * bnbPrice;

            if (userVal >= sponsorMin) {
                setViewable(true);
            } else {
                setViewable(false);
            }
        }
    }, [wallet.balances, projectsMap, bnbPrice])


    useEffect(() => {

        setOwnerBalance(0);
        setOwnerPercentage(0);
        setPoolDiff(0);
        setPoolDiffPercentage(0);
        setLiquidityScore("");

        const c = new dataseed.eth.Contract(sd_abi, project.contract_address);

        if (owner) {
            c.methods.balanceOf(owner).call((err, resp) => {
                if (err) {
                    return;
                }

                const ownerBal = resp / Math.pow(10, 9) || 0;
                setOwnerBalance(ownerBal);
                setOwnerPercentage((ownerBal / stats.supply * 100).toFixed(2));
            })
        }

        c.methods.balanceOf(project.contract_address).call((err, resp) => {
            if (err) {
                return;
            }
            const poolBal = resp / Math.pow(10, 9) || 0;
            setPoolDiff(Math.abs(stats.liquidity - poolBal));
            setPoolDiffPercentage((Math.abs(stats.liquidity - poolBal) / stats.supply * 100).toFixed(2));
        })

        const lpLevel = stats.fbnb - stats.backing;

        if (lpLevel < 1) {
            setLiquidityScore('VERY HIGH');
        }

        if (lpLevel >= 1 && lpLevel < 10) {
            setLiquidityScore('HIGH');
        }

        if (lpLevel >= 10 && lpLevel < 50) {
            setLiquidityScore('MEDIUM');
        }

        if (lpLevel >= 50 && lpLevel < 100) {
            setLiquidityScore('LOW');
        }

        if (lpLevel >= 100) {
            setLiquidityScore('VERY LOW');
        }

    }, [owner, project.symbol, project.contract_address, stats]);

    return <Paper elevation={0}>
        {
            viewable ? <List disablePadding dense subheader={<Typography>Divvy Factor <Typography variant="caption" style={{float: 'right'}}>sponsored by
                    DEGEN</Typography></Typography>}>
                    {owner && <StatDetail
                        avatar={<AccountBoxIcon sx={{color: project.color}}/>}
                        primary={<Link href={`https://bscscan.com/address/${owner}`} target="_blank"
                                       underline="none">{owner.substr(0, 6) + "..." + owner.substr(-6)}</Link>}
                        secondary="Pool Owner"
                    />
                    }
                    <StatDetail
                        avatar={<PieChartIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(ownerBalance)} ${project.symbol} (${ownerPercentage}%)`}
                        secondary="Pool Owner Share"
                    />
                    <StatDetail
                        avatar={<RuleIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(poolDiff)} ${project.symbol} (${poolDiffPercentage}%)`}
                        secondary="Inside SmartSwap/SmartLoan"
                    />
                    <StatDetail
                        avatar={<InvertColorsIcon sx={{color: project.color}}/>}
                        primary={`${liquidityScore} (${DivvyCryptoFormat(stats.fbnb - stats.backing)} fBNB)`}
                        secondary="Liquidity Risk Level"
                    />
                </List>
                :
                wallet.address ?
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography textAlign="center">To access Divvy Factor, you need to
                                hold <br/><strong>{DivvyUSDFormat(sponsorMin)}</strong> worth of DEGEN.<br/><Typography
                                    textAlign="center"
                                    variant="caption">{DivvyCryptoFormat(tokensNeeded)} DEGEN</Typography></Typography>


                        </Grid>
                    </Grid>
                    :
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0xCfE058c0646c0Eec7e042744D9B8787d0f047a34/logo.png" />
                        </ListItemAvatar>
                        <ListItemText primary={<Typography>Connect wallet for <strong>Divvy Factor</strong></Typography>} secondary="Sponsored by DEGEN" />
                    </ListItem>
        }
    </Paper>
}