import {useEffect, useState} from "react";
import {
    Button, Checkbox,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle, FormControlLabel, FormGroup,
    Typography
} from "@mui/material";

/**
 * FLAGS
 * 1 = Active Listing
 * 2 = Bypass Min
 * 4 =
 * 8 =
 * 16 =
 */


export default function SetFlagsDialog(props) {

    const {contract_address, flags, onSave} = props;

    const [open, setOpen] = useState(false);
    const [val, setVal] = useState(flags);

    useEffect(()=>{
        if(open)setVal(flags);
    }, [open, flags])

    const handleChange = (ev, flag) => {
        let temp = val;
        if(ev.target.checked){
            setVal(temp |= flag);
        } else {
            setVal(temp &= ~flag);
        }
    }

    return <>
        <Typography onClick={() => setOpen(true)}>{flags}</Typography>
        <Dialog open={open}>
            <DialogTitle>Set Flags</DialogTitle>
            <DialogContent>
                <Typography>Update Flags for: {contract_address}</Typography>
                <Typography>Flags: {val}</Typography>
                <FormGroup>
                    <FormControlLabel control={<Checkbox checked={val&1 ? true : false} onChange={(ev)=>handleChange(ev,1)} />} label="Active" />
                    <FormControlLabel control={<Checkbox checked={val&2 ? true : false} onChange={(ev)=>handleChange(ev,2)} />} label="Bypass 1%" />
                </FormGroup>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button onClick={() => {
                    onSave(contract_address, val);
                    setOpen(false)
                }}>Submit</Button>
            </DialogActions>
        </Dialog>
    </>;
}