import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    bnbPrice: 0,
    projects: [],
    projectsMap: {},
    wallet: {
        address: undefined,
        chain: undefined,
        type: undefined,
        balances: {}
    },
    loaded: false,
    currentLoaded: 0
};

const divvySlice = createSlice({
    name: 'divvy',
    initialState,
    reducers: {

        updateBNBPrice(state, action) {
            state.bnbPrice = parseFloat(action.payload.price);
        },

        updateProjects(state, action) {
            state.projects = action.payload;
        },

        updateProjectsMap(state, action) {
            if (!state.projectsMap[action.payload.contract_address]) {
                state.projectsMap[action.payload.contract_address] = {}
            }

            state.projectsMap[action.payload.contract_address] = {...state.projectsMap[action.payload.contract_address], ...action.payload};

            if(!state.loaded){
                state.currentLoaded = state.currentLoaded + 1;
                if(state.currentLoaded === state.projects.length) {
                    state.loaded = true;
                }
            }
        },

        updatePersonalBalance(state, action) {
            if (!state.wallet.balances[action.payload.contract_address]) {
                state.wallet.balances[action.payload.contract_address] = 0
            }

            state.wallet.balances[action.payload.contract_address] = action.payload.balance;
        },

        updateWalletChain(state, action) {
            state.wallet.chain = action.payload
        },

        updateWalletAddress(state, action) {
            state.wallet.address = action.payload
        },

        updateWalletProviderType(state, action) {
            state.wallet.type = action.payload
        },

        resetWallet(state) {
            state.wallet.chain = undefined;
            state.wallet.address = undefined;
            state.wallet.type = undefined;
            state.wallet.balances = {};
        },

        updateFaucetRewards(state, action) {
            if(
                state.projectsMap[action.payload.contract_address] &&
                state.projectsMap[action.payload.contract_address].faucet
            ){
                state.projectsMap[action.payload.contract_address].faucet.reflections = action.payload.faucet_rewards
            }
        },

        updateProjectsLocker(state, action) {
            if (!state.projectsMap[action.payload.contract_address]) {
                state.projectsMap[action.payload.contract_address] = {}
            }

            const {locked, reflections, time, contract_address} = action.payload;

            state.projectsMap[contract_address].locker = {
                ...state.projectsMap[contract_address].locker,
                locked,
                reflections,
                time
            }
        },

        updateProjectsFaucet(state, action) {
            if (!state.projectsMap[action.payload.contract_address]) {
                state.projectsMap[action.payload.contract_address] = {}
            }

            const {locked, reflections, contract_address} = action.payload;

            state.projectsMap[contract_address].faucet = {
                ...state.projectsMap[contract_address].faucet,
                locked,
                reflections
            }
        }
    }
});

export const {
    updateBNBPrice,
    updateProjects,
    updateProjectsMap,
    updatePersonalBalance,
    updateWalletChain,
    updateWalletAddress,
    updateWalletProviderType,
    resetWallet,
    updateFaucetRewards,
    updateProjectsLocker,
    updateProjectsFaucet
} = divvySlice.actions;

export default divvySlice.reducer;
