import {useEffect, useState} from "react";
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle, FormControl,
    TextField,
    Typography
} from "@mui/material";

export default function SetFaucetDialog(props) {

    const {contract_address, faucet_address, onSave} = props;

    const [open, setOpen] = useState(false);
    const [val, setVal] = useState(faucet_address);

    useEffect(()=>{
        if(open)setVal(faucet_address);
    }, [open, faucet_address])

    return <>
        <Typography onClick={() => setOpen(true)}>...{faucet_address.substr(-6)}</Typography>
        <Dialog open={open}>
            <DialogTitle>Set Faucet</DialogTitle>
            <DialogContent>
                <Typography>Update Faucet for: {contract_address}</Typography>
                <FormControl fullWidth>
                    <TextField
                        label="Locker Address"
                        variant="outlined"
                        value={val}
                        defaultValue={faucet_address}
                        onChange={(ev) => {
                            setVal(ev.target.value)
                        }}
                    />
                </FormControl>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button onClick={() => {
                    onSave(contract_address, val);
                    setOpen(false)
                }}>Submit</Button>
            </DialogActions>
        </Dialog>
    </>;
}