import {Avatar, Grid, ListItem, ListItemAvatar, ListItemText} from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import {Link as RouterLink} from "react-router-dom";
import {useSelector} from "react-redux";
import {DivvyUSDFormat} from "../utils";

export default function ProjectHeader(props) {

    const {project} = props;

    const {projectsMap, bnbPrice} = useSelector(state => state.divvy);

    const stats = projectsMap[project.contract_address]?.stats;

    if (!stats) return null;

    const {price} = stats;

    return <Grid container spacing={2}>
        <Grid item style={{marginTop: 15, marginLeft: 10}}>
            <RouterLink
                to={`/`}
                style={{textDecoration: 'none', color: 'inherit'}}
            >
                <ArrowBackIcon/>
            </RouterLink>
        </Grid>
        <Grid item style={{flex: 1, marginRight: 20}}>
            <ListItem disablePadding>
                <ListItemAvatar>
                    <Avatar
                        src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract_address}/logo.png`}
                        sx={{width: 40, height: 40}}
                    />
                </ListItemAvatar>
                <ListItemText primary={project.name} secondary={project.symbol} />
                <ListItemText
                    primary={DivvyUSDFormat(price * bnbPrice)}
                    primaryTypographyProps={{textAlign: 'right'}}
                />
            </ListItem>
        </Grid>
    </Grid>
}