import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {Avatar, List, ListItem, ListItemAvatar, ListItemText, styled} from "@mui/material";
import millify from "millify";
import {Link as RouterLink} from "react-router-dom";
import PendingIcon from "@mui/icons-material/Pending";
import DivvyLoading from "../components/DivvyLoading";
import {DivvyUSDFormat} from "../utils";
import LockIcon from '@mui/icons-material/Lock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';

const DivvyAvatar = styled(Avatar)({
    backgroundColor: 'transparent'
})

const millifyConfig = {
    space: true,
    precision: 2,
    units: ['', 'K', 'M', 'B', 'T']
}

export default function HomePage() {

    const {loaded, projects, projectsMap, bnbPrice} = useSelector(state => state.divvy);
    const [open, setOpen] = useState(true);

    useEffect(() => {
        if (loaded) {
            setOpen(false)
        }
    }, [loaded])

    const list = [...projects];

    list.sort((a, b) => {

        if (!projectsMap[a.contract_address] || !projectsMap[b.contract_address]) {
            return null;
        }
        const a2 = projectsMap[a.contract_address];
        const b2 = projectsMap[b.contract_address];

        if (!a2.stats || !b2.stats) {
            return null;
        }

        if ((a2.stats.circulation * a2.stats.price) < (b2.stats.circulation * b2.stats.price)) {
            return 1;
        }
        if ((a2.stats.circulation * a2.stats.price) > (b2.stats.circulation * b2.stats.price)) {
            return -1;
        }
        return 0;
    });

    return <>
        <DivvyLoading open={open} label="Preparing Awesome Sauce..."/>
        <List>
            {
                list.map(project => {

                    const stats = projectsMap[project.contract_address]?.stats;

                    if (!stats) return null;

                    const {price, circulation} = stats;

                    const pending = (stats.divvyBalance < (stats.supply * .01)) && !(!!(project.flags & 2));

                    return <RouterLink
                        key={project.symbol}
                        to={`/${project.symbol}`}
                        style={{textDecoration: 'none', color: 'inherit'}}
                    >
                        <ListItem>
                            <ListItemAvatar>
                                <DivvyAvatar>
                                    {
                                        pending ?
                                            <PendingIcon color="primary"/>
                                            :
                                            <img
                                                alt={`${project.name} Logo`}
                                                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract_address}/logo.png`}
                                                width={40}/>
                                    }
                                </DivvyAvatar>
                            </ListItemAvatar>
                            <ListItemText primary={project.name} secondary={
                                <>
                                    {project.symbol}
                                    {project.locker_address !== '0x0000000000000000000000000000000000000000' &&
                                        <LockIcon sx={{fontSize: 15, marginLeft: 1}}/>}
                                    {project.faucet_address !== '0x0000000000000000000000000000000000000000' &&
                                        <LocalAtmIcon sx={{fontSize: 15}}/>}
                                </>
                            }/>
                            <ListItemText
                                primary={`${DivvyUSDFormat((price * bnbPrice) || 0)}`}
                                primaryTypographyProps={{textAlign: 'right'}}
                                secondary={`$${millify((price * bnbPrice * circulation) || 0, millifyConfig)}`}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </RouterLink>
                })
            }
        </List>
    </>
}