import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import StatDetail from "./StatDetail";
import {useEffect, useState} from "react";

export default function PersonalStats(props) {

    const {project} = props;
    const {bnbPrice, wallet, projectsMap} = useSelector(state => state.divvy);
    const {stats} = projectsMap[project.contract_address];
    const balance = wallet.balances[project.contract_address];

    // sponsor check
    const [viewable, setViewable] = useState(false);
    const [tokensNeeded, setTokensNeeded] = useState(0);
    const sponsorToken = '0x820671E3647d8aaa22Feab05aaA14AB1665e06e1';
    const sponsorMin = 10;// in USD
    useEffect(() => {
        setViewable(false);
        setTokensNeeded(sponsorMin / bnbPrice / projectsMap[sponsorToken]?.stats?.price)
        if (wallet.balances[sponsorToken] && projectsMap[sponsorToken]?.stats?.price && bnbPrice) {

            const userVal = wallet.balances[sponsorToken] * projectsMap[sponsorToken]?.stats?.price * bnbPrice;

            if (userVal >= sponsorMin) {
                setViewable(true);
            } else {
                setViewable(false);
            }
        }
    }, [wallet.balances, projectsMap, bnbPrice])

    return <Paper elevation={0}>
        {
            viewable ? <List disablePadding dense subheader={<Typography>My Wallet <Typography variant="caption" style={{float: 'right'}}>sponsored by
                    FOOK</Typography></Typography>}>

                    <StatDetail
                        avatar={<AccountBalanceWalletIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(balance)} ${project.symbol}`}
                        secondary="Balance"
                    />
                    <StatDetail
                        avatar={<AttachMoneyIcon sx={{color: project.color}}/>}
                        primary={`${DivvyUSDFormat(balance * stats.price * bnbPrice)}`}
                        secondary="Value USD"
                    />
                    <StatDetail
                        avatar={<BNBIcon color={project.color}/>}
                        primary={`${DivvyCryptoFormat(balance * stats.price)} BNB`}
                        secondary="Value BNB"
                    />
                </List>
                :
                wallet.address ?
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography textAlign="center">To access My Wallet, you need to
                                hold <br/><strong>{DivvyUSDFormat(sponsorMin)}</strong> worth of FOOK.<br/><Typography
                                    textAlign="center"
                                    variant="caption">{DivvyCryptoFormat(tokensNeeded)} FOOK</Typography></Typography>


                        </Grid>
                    </Grid>
                    :
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0x820671E3647d8aaa22Feab05aaA14AB1665e06e1/logo.png" />
                        </ListItemAvatar>
                        <ListItemText primary={<Typography>Connect wallet for <strong>My Wallet</strong></Typography>} secondary="Sponsored by FOOK" />
                    </ListItem>
        }
    </Paper>
}