import {Avatar, Typography} from "@mui/material";
import {useSelector} from "react-redux";
import MaterialTable from "material-table";
import Web3 from "web3";
import divvy_listings_abi from "../abis/DivvyListings.json";
import SetLockerDialog from "../components/dialogs/SetLockerDialog";
import SetFaucetDialog from "../components/dialogs/SetFaucetDialog";
import SetFlagsDialog from "../components/dialogs/SetFlagsDialog";

const DivvyListingContract = '0x5cf76803e834B0D16172ECeD1175F55c4881eecA';

export default function Admin() {
    const {projects, wallet} = useSelector(state => state.divvy);

    const _projects = [];
    projects.map(project => {
        _projects.push({...project});
        return project;
    });

    const web3 = new Web3(window.provider);
    const contract = new web3.eth.Contract(divvy_listings_abi, DivvyListingContract);

    const setLocker = (contract_address, locker_address) => {
        contract.methods.setLocker(contract_address, locker_address).send({from: wallet.address}, (err, resp) => {
            if (err) {
                console.log(err);
                return;
            }
            console.log(resp);
        })
    }

    const setFaucet = (contract_address, faucet_address) => {
        contract.methods.setFaucet(contract_address, faucet_address).send({from: wallet.address}, (err, resp) => {
            if (err) {
                console.log(err);
                return;
            }
            console.log(resp);
        })
    }

    const setFlags= (contract_address, flags) => {
        contract.methods.setFlags(contract_address, flags).send({from: wallet.address}, (err, resp) => {
            if (err) {
                console.log(err);
                return;
            }
            console.log(resp);
        })
    }

    return <MaterialTable
        columns={[
            {
                title: '',
                render: (rowData) => {
                    return <>
                        <Avatar>
                            <img
                                alt={`${rowData.name} Logo`}
                                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${rowData.contract_address}/logo.png`}
                                width={40}/>
                        </Avatar>
                    </>
                }
            },
            {
                title: 'Name',
                field: 'name'
            },
            {
                title: 'Symbol',
                field: 'symbol'
            },
            {
                title: 'Color',
                field: 'color'
            },
            {
                title: 'Creator',
                field: 'creator',
                render: (rowData => {
                    return <Typography>...{rowData.creator.substr(-6)}</Typography>
                })
            },
            {
                title: 'Contract',
                field: 'contract_address',
                render: (rowData => {
                    return <Typography>...{rowData.contract_address.substr(-6)}</Typography>
                })
            },
            {
                title: 'Locker',
                field: 'locker_address',
                render: (rowData => {
                    return <SetLockerDialog
                        contract_address={rowData.contract_address}
                        locker_address={rowData.locker_address}
                        onSave={(ca, la)=>{
                            setLocker(ca, la);
                        }} />
                })
            },
            {
                title: 'Faucet',
                field: 'faucet_address',
                render: (rowData => {
                    return <SetFaucetDialog
                        contract_address={rowData.contract_address}
                        faucet_address={rowData.faucet_address}
                        onSave={(ca, fa)=>{
                            setFaucet(ca, fa);
                        }} />
                })
            },
            {
                title: 'Floor',
                field: 'floor'
            },
            {
                title: 'Flags',
                field: 'flags',
                render: (rowData => {
                    return <SetFlagsDialog
                        contract_address={rowData.contract_address}
                        flags={rowData.flags}
                        onSave={(ca, f)=>{
                            setFlags(ca, f);
                        }} />
                })
            }
        ]}
        data={_projects}
        title="Admin"
        options={{
            paging: true,
            pageSize: 10,
            pageSizeOptions: [10,20,50]
        }}
    />;
}